@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
.homecontent {
  width: 100%;
  min-height: calc(100vh - 120px);
  height: auto;
  margin-top: 10px;
  padding: 0 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #1d1d1d;
  color: #fff;
}
.hero-content {
  width: 50%;
}
.hero-heading {
  font-size: 5rem;
  text-transform: capitalize;
  font-weight: 500;
}
.highlight {
  color: #ff3559;
}
.profession {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: block;
  margin: 10px 0 20px;
  margin-left: auto;
  text-transform: capitalize;
  position: relative;
  padding: 10px 20px;
  color: #1d1d1d;
  z-index: 2;
}
.profession::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #e3e3e3;
  z-index: -1;
  transform: skewX(10deg);
}
.profession::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100px;
  width: 100px;
  height: 2px;
  background: #e3e3e3;
  z-index: -1;
}
.info {
  line-height: 30px;
  font-size: 20px;
  margin-bottom: 50px;
}
.btn {
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 50px;
  background: #ff3559;
  color: #fff;
  text-transform: capitalize;
  border: none;
}
.profile-Image {
  width: 330px;
  opacity: 0.9;
  border-radius: 50% 50% 20px 50%;
}
/* ==tablet view == */
@media (max-width: 996px) {
  html {
    font-size: 14px;
  }
  .homecontent {
    flex-direction: column-reverse;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-bottom: 50px;
  }
  .profile-Image {
    width: 250px;
    margin: 40px;
  }
  .hero-content {
    width: 70%;
    min-width: 350px;
    text-align: center;
  }
  .homecontent .title {
    width: 50%;
    margin-top: 0;
    text-align: left;
    font-size: 25px;
  }
}

@media (max-width: 411px) {
  .profile-Image {
    width: 200px;
  }
  .hero-heading {
    font-size: 4rem;
  }
  .profession {
    left: -65px;
  }
  .info {
    padding: 0 30px;
  }
}
@media (max-width: 280px) {
  .profile-Image {
    width: 200px;
  }
  .profession {
    left: -55px;
  }
  .hero-heading {
    font-size: 3rem;
  }
  .info {
    text-align: center;
    padding: 0 70px;
  }
}

.logo a {
  text-decoration: none;
  color: #fff;
}
.line-1 {
  height: 1.5px;
  width: 2rem;
  background: #fff;
  margin-top: 3px;
}
.line-2 {
  height: 1.5px;
  width: 3rem;
  background: #fff;
  margin-top: 3px;
}
nav.navbar {
  width: 100%;
  padding: 0 8%;
  height: 100vh -60px;
  color: #fff;
  background: #1d1d1d;
}
.nav-wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.list-wrapper img {
  opacity: 0;
  z-index: 4;
  width: 50px;
  right: 30px;
  padding: 8px;
  position: absolute;
  border-radius: 999px;
  transition: all 0.5s ease-in-out;
}
.list-wrapper {
  display: flex;
  align-items: center;
}
.list-wrapper ul {
  background: #1d1d1d;
  opacity: 0.8;
  top: 0;
  left: -100vw;
  z-index: 3;
  width: 100%;
  min-height: auto;
  padding-top: 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-bottom: 0;
  text-align: center;
  position: absolute;
  transition: all 0.5s ease-in-out;
}
.list-wrapper ul li {
  list-style: none;
  margin-bottom: 15px;
}

.list-wrapper ul li::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #fff;
}
.list-wrapper ul li::after {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 100%;
  height: 1px;
  background: #fff;
}
.list-wrapper ul li a {
  display: block;
  font-size: 24px;
  padding: 10px 0;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  color: #fff;
}

@media (min-width: 800px) {
  .list-wrapper img {
    display: none;
  }
  .list-wrapper ul {
    display: flex;
    flex-direction: row;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: left;
    position: relative;
    background-color: transparent;
  }
  .list-wrapper ul li {
    display: inline-block;
    margin-bottom: 0;
  }
  .list-wrapper ul li:not(:last-child) {
    margin-left: 10px;
  }
  .list-wrapper ul li a {
    padding: 5px;
  }
  .list-wrapper ul li a:hover {
    padding: 5px;
    transform: scale(0.8);
    background: transparent;
  }
}

/* === about section styles === */
.about {
  width: 100%;
  height: auto;
  padding: 50px 10vw;
}
.indicator {
  position: fixed;
  right: 20px;
  top: 50%;
  width: 45px;
}

.about-image {
  width: 400px;
  /* height: 300px; */
  border-radius: 10px 50%;
  background: rgb(201, 180, 184);
}

.about-me-container {
  margin: 150px 0 100px;
  width: 100%;
  display: grid;
  grid-template-columns: 40% 60%;
  grid-gap: 50px;
}
.left-col,
.right-col {
  position: relative;
}
.left-col::before {
  content: "Yes, I can help!";
  text-transform: capitalize;
  position: absolute;
  right: -24px;
  top: -13px;
}
.left-col::after {
  content: "";
  position: absolute;
  top: 1px;
  right: 75px;
  width: 88px;
  height: 2px;
  background: #fff;
  transform-origin: right;
  transform: rotate(-30deg);
}
.about-para {
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 35px;
  margin-bottom: 40px;
}
.skill-container {
  position: relative;
  margin-top: 100px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
.skill-card-html,
.skill-card-css,
.skill-card-scss,
.skill-card-js,
.skill-card-react,
.skill-card-mtui,
.skill-card-bootstrap,
.skill-card-firebase {
  height: 200px;
  border-radius: 10px;
  border: 1px solid #464646;
  text-align: center;
  position: relative;
  cursor: pointer;
  transition: 0.5s;
}
.skill {
  font-size: 2rem;
  color: #464646;
  line-height: 200px;
}
body {
  --bgHTML: #f06529;
  --bgCSS: #83cd29;
  --bgSCSS: #326690;
  --bgJS: #f7df1e;
  --bgREACT: #5ed9fb;
  --bgMTUI: #f06529;
  --bgBTSTRAP: #f06790;
  --bgFIREBASE: #ffa000;
}
.skill-card-html:hover {
  background: var(--bgHTML);
}
.skill-card-css:hover {
  background: var(--bgCSS);
}
.skill-card-scss:hover {
  background: var(--bgSCSS);
}
.skill-card-js:hover {
  background: var(--bgJS);
}
.skill-card-react:hover {
  background: var(--bgREACT);
}
.skill-card-bootstrap:hover {
  background: var(--bgBTSTRAP);
}
.skill-card-firebase:hover {
  background: var(--bgFIREBASE);
}
.skill-card-mtui:hover {
  background: var(--bgMTUI);
}
.skill-card-html:hover .skill,
.skill-card-css:hover .skill,
.skill-card-scss:hover .skill,
.skill-card-js:hover .skill,
.skill-card-bootstrap:hover .skill,
.skill-card-firebase:hover .skill,
.skill-card-mtui:hover .skill,
.skill-card-react:hover .skill {
  color: #fff;
}
@media (max-width: 1024px) {
  .about-me-container {
    grid-template-columns: 45% 50%;
    grid-gap: 80px;
  }
}
@media (max-width: 996px) {
  .about-me-container {
    grid-template-columns: 1fr;
  }
  .indicator {
    width: 30px;
  }
  .left-col {
    margin: auto;
    width: 50%;
    min-width: 320px;
  }
  .skill-container {
    grid-template-columns: repeat(2, 1fr);
  }
  .right-col {
    text-align: center;
  }
}

.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 3rem;
  padding: 3rem 15px 15px 15px;
}
ul.project-list {
  width: 100%;
  display: flex;
}
.project-list li {
  padding: 20px;
  width: 45%;
  list-style: none;
}
ul li {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
ul li a img {
  border-radius: 10px;
  width: 100%;
  min-height: 290px;
  height: 290px;
  object-fit: cover;
  border: 1.5px solid #ff4d6d;
}

.title {
  position: absolute;
  padding: 10px;
  top: 20px;
  left: 0;
  border-radius: 10px;
  opacity: 0;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 290px;
  height: 290px;

  transition: 0.5s;
}

.title:hover {
  opacity: 1;
  color: #fff;
  opacity: 0.8;
  background: #b67b86;
  transform: scale(1.1);
  transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}
.title h1 {
  color: #fff;
  opacity: 1;
}
@media (max-width: 800px) {
  ul.project-list {
    display: block;
  }
  .project-list li {
    width: 100%;
  }
}

.contact {
  margin-top: 50px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sub-heading {
  padding: 0 40px;
}
.contact-form {
  width: 80%;
  margin-top: 50px;
  position: relative;
}
.contact-form input,
.message {
  width: 100%;
  display: block;
  height: 50px;
  padding: 20px;
  border-radius: 5px;
  background: #000;
  color: #fff;
  outline: none;
  border: none;
  margin: 30px 0;
  text-transform: capitalize;
  resize: none;
}
.message {
  height: 200px;
}
.contact-form .name {
  display: flex;
  justify-content: space-between;
}
.name input {
  width: 49%;
  margin: 0;
}
.contact-form .btn {
  display: block;
  margin: auto;
  cursor: pointer;
}

footer {
  background: #1d1d1d;
  width: 100%;
  padding: 0 8%;
}
footer img {
  max-width: 30px;
  height: 30px;
  border-radius: 50%;
}

footer a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Quicksand";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(104, 98, 175);
}

/* hide scroll bar for chrome safari and opera */
body::-webkit-scrollbar {
  display: none;
}
html {
  scroll-behavior: smooth;
}

body {
  -ms-overflow-style: none;
  scrollbar-width: none;
  background: #1d1d1d;
  color: #fff;
}

.heading {
  text-align: center;
  font-weight: 500;
  font-size: 3.5rem;
  text-transform: capitalize;
  color: #fff;
}
.sub-heading {
  text-align: center;
  font-size: 1.4rem;
  margin: 0 10px;
  opacity: 0.7;
}
.seperator {
  width: 25%;
  margin: 20px auto;
  position: relative;
}
.seperator::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #fff;
}
.seperator::after {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 0;
  width: 100%;
  height: 1px;
  background: #fff;
}
@media (max-width: 996px) {
  .sub-heading {
    padding: 25px;
  }
}
@media (max-width: 540px) {
  html {
    font-size: 12px;
  }
  .seperator {
    margin: 0px auto;
  }
  p,
  .sub-heading,
  .about-para,
  .left-col::before {
    font-size: 1.4rem;
  }

  .about-image {
    width: 300px;
  }
  .skill-container,
  .project {
    grid-template-columns: 1fr;
  }
  .skill {
    font-size: 2.5rem;
  }
  .project-name {
    font-size: 3rem;
  }
  .name {
    flex-direction: column;
  }
  .name input {
    width: 100%;
  }
  .first-name {
    margin-bottom: 20px !important;
  }
}
@media (max-width: 414px) {
  .about-image {
    width: 250px;
  }
  .right-col {
    width: 100%;
  }
  .left-col {
    margin: 0;
    width: 100%;
    min-width: 0;
  }

  .left-col::after {
    top: -24px;
  }
  .left-col::before {
    top: -40px;
  }
}
@media (max-width: 280px) {
  .heading {
    position: relative;
    width: 100%;
    margin: -20px 0 0 0;
    padding: 0px;
    font-size: 3rem;
  }
  .seperator {
    margin: 0px auto;
  }
  .about-image {
    margin-top: 0;
    width: 200px;
  }
  .right-col {
    padding: 0 10px;

    text-align: center;
  }
}

